import Carousel from './Carousel'
import Box from './box/Box';

function Boxes(props){
    let topMovies = props.movies
    let newMovies = []
    topMovies.map((item)=> {newMovies.unshift(item)})
    return(newMovies.slice(0,3).filter(item => item.image).map((item ) => <Box title={item.title} img={item.image} />))
}

function Header(props) {

    return (
        <div className='container px-4 mb-5' >
            <div className="row">
                <div className="col-12 col-lg-8">
                    <Carousel />
                </div>
                <div className="col mt-2 p-0">
                    <h5 className='text-warning mb-3' >Your last visited movies</h5>
                    <div className='bg-box p-3 h-100' >
                        <Boxes movies={props.movies} />
                        <a className='bold' href='#' >
                            Browse trailers
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-chevron-right" viewBox="0 0 16 16">
                                <path fillRule="evenodd" d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z" />
                            </svg>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    )
}


export default Header