import {Link} from 'react-router-dom'

function SearchBar(props) {

    function checker(event){
        document.getElementById('searchBar').value ? '' : (alert('input error'), window.location.reload())
    }

    return (
        <form className="d-flex w-100 m-0">
            <input id='searchBar' onChange={props.handler}  className="form-control border-start-0 rounded-0 bg-light rounded-end me-2" type="search" placeholder="Search" aria-label="Search" />
            <Link to='/search' ><button name='searchButton' onClick={checker} className='btn btn-outline-warning' type='submit'>Search</button></Link>
        </form>
    )
}

export default SearchBar