import './App.css';
import Navigation from './components/navigation/Navigation';
import Header from './components/header/Header';
import Card from './components/card/Card';
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { useState } from 'react';
import Search from './components/search/Search';
import Movie from './components/movie/Movie';
import {Animated} from 'react-animated-css'


function App() {
  const [film, setFilm] = useState([])
  const [targetMovie, setTargetMovie] = useState({})
  const [lastMovies, setLastMovies] = useState(JSON.parse(localStorage.getItem("lastMovies")) || [''])

  document.getElementsByTagName('body')[0].className = 'bg-black text-light';
   const filmSearch = (event) =>{
    const API_KEY = '8e5fc4995685fb045eaa25ae318ca552'
    const URL = `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&language=en-US&page=1&include_adult=false&query=${event.target.value.length > 0 ? event.target.value : 'Disne' }`
    fetch(URL).then(response => response.json()).then(data => setFilm(data.results))
  }
  localStorage.setItem('lastMovies',JSON.stringify(lastMovies))
  return (
    <Router>
      <Navigation handler={filmSearch} />
      <Switch>
        <Route exact path='/' >
          <Animated animationIn='slideInRight' >
            <Header movies={lastMovies} />
            <Card/>
          </Animated>
        </Route>
        <Route exact path='/search' >
          <Animated animationIn='zoomIn' >
            <div className="container px-4">
              <div className="row">
                {film.slice(0,12).filter(item => item.poster_path).filter(item => item.overview).map(item => <Search lastMovies={lastMovies} setLastMovies={setLastMovies} action={setTargetMovie} movie={item} />)}
              </div>
            </div>
          </Animated>
        </Route>
        <Route exact path='/movie'>
          <Movie movie={targetMovie} />
        </Route>
        <Route path='/404'>
          <h1 className='text-light text-center' >404 ERROR <br/> NOT FOUND</h1>
        </Route>
        <Redirect from='*' to='/404'/>
      </Switch>
    </Router>
  );
}

export default App;
