import {Link} from 'react-router-dom';
import {Animated} from 'react-animated-css';

function Search(props) {
    let {original_title, overview, poster_path} = props.movie
    function targetFound(event){

        props.setLastMovies(props.lastMovies.concat({"image":poster_path,"title":original_title}))
        props.action(props.movie)
    }
    return(
            <div className='col-12 col-sm-6 col-lg-3 mx-auto my-2 mx-1 text-light border border-light p-0' >
                <Animated animationIn='zoomIn' >
                <div className='border border-bottom' >
                    <img src={"https://image.tmdb.org/t/p/w500/"+poster_path} className='w-100' alt=""/>
                </div>
                <div className='p-2' >
                    <h4 className='text-center' >{original_title}</h4>
                    <p >{overview.substring(0,115)}...</p>
                    <Link to='/movie' onClick={targetFound} className='btn btn-outline-warning text-warning d-block ms-auto' style={{width:'5rem'}}>
                       Details...</Link>
                </div>
                </Animated>
            </div>
    )
}
 
export default Search